import {Injectable} from 'angular2/core';
import {Todo} from '../models/todo';

import {Http} from 'angular2/http';

@Injectable()
export class TodoService {
  // private todos:Array<Todo> = [
  //   new Todo('Todo number 1', false),
  //   new Todo('Todo number 2', false),
  //   new Todo('Todo number 3', false)
  // ];

  constructor(public http: Http) {
    console.log('Todo Service Created.', http);
  }


  getTodos() {
    return this.http.get('http://localhost/todo')
      .map((responseData: any) => {
        return responseData.json();
      })
      .map((todos: Array<any>) => {
        let result:Array<Todo> = [];
        if (todos) {
          todos.forEach((todo) => {
            result.push(new Todo(todo.id, todo.description, todo.priority, todo.dueDate, todo.complete));
          });
        }
        return result;
      });
  }


  // getTodos():Array<Todo> {
  //   return this.todos;
  // }

  // addTodo(name:string) {
  //   this.todos.push(new Todo(name, false));
  // }
}
